import React, {Component} from 'react';
import '../style/index.scss';
import Tree from './Tree';
import NavBar from './NavBar';
import list from '../../.list.json';

export default class App extends Component {

  render() {
    return (
      <div>
        <NavBar />
        <div className="tree">
          <Tree list={list} />
        </div>
      </div>

    );
  }
}



