import React, {Component} from 'react';

export default class NavBar extends Component {
  render() {
    return (
      <nav className="navbar navbar-dark bg-success">
        <span className="navbar-brand">族譜</span>
        <button className="navbar-toggler hidden-md-up" type="button" data-toggle="collapse"
                data-target="#navTog"></button>
        <div className="collapse navbar-toggleable-sm" id="navTog">
          <ul className="nav navbar-nav float-lg-right">
            <li className="nav-item"><a href="#" className="nav-link">註冊</a></li>
            <li className="nav-item"><a href="#" className="nav-link">登入</a></li>
            <li className="nav-item"><a href="#" className="nav-link">聯絡管理員</a></li>
          </ul>
        </div>
      </nav>
    );
  }
}


